@use '_mixin' as m;



.logo {
  font-family: var(--second-family);
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: m.toRem(10);
  @media (max-width: m.toEm(768)) {
    gap: m.toRem(5);
  }
  // .logo__icon

  &__icon {
    flex-shrink: 0;
    width: m.toRem(30);
    height: m.toRem(30);
    @media (max-width: m.toEm(768)) {
      width: m.toRem(25);
      height: m.toRem(25);
    }
  }

  // .logo__text

  &__text {
    font-weight: 700;
    font-size: m.toRem(24);
    line-height: 110%;
    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(20);
    }
  }
}